
.wrapper {
  height:calc(100vh - 3rem);
  width: 100vw;
  border: 3px solid rgb(17, 14, 14);
  position: absolute;
  top: 0;
  background: #161616 0% 0% no-repeat padding-box;
  opacity: 0.8;
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
}


.rows{
  width:60%;
  margin:auto;
  margin-top:60px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-items: center;
  justify-content: center;
}

@media(max-width:768px){
  .rows{
    width:100%;
  }
}

.menu-card{
  background-color: rgb(73, 71, 71);
  width:250px;
  height:150px;
  margin-left:20px;
  margin-right: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  position:relative;
}
.menu-card:hover{
  background-color: #333333;
 
}
.containers {
  overflow: auto;
  height:calc(100vh - 8rem);
}
.menu-content{
  padding:23px;
  color:white;
  text-transform: uppercase;
  font-size: 13px;
  position:relative;
  text-align: center;
}
 @media(max-width:468px){
   .menu-card{
     width:290px;
     height:250px;
   }
 }

.heading_menu_list {
  font-weight: 500;
  font-size: 16px;
  color: white;
  font-family: Montserrat;
  padding-top: 2rem;
  margin-left: 0.5rem;
  height: 4rem;
  text-align: center;
  margin-bottom: 10px;
}
.heading_menu_list img {
  position: absolute;
  top: 1rem;
  right: 1rem;
  text-align: center;
}
.footerBar {
  height: 25vh;
  position: fixed;
  bottom: 0;
  width: 100vw;
  opacity: 100 !important;
  background: black;
}


.menu_images {
   border: none !important;
   height: 28px;
   margin-bottom: 4px;
 
}

.menu_image {
 border: none !important;
  height: 28px;
  margin-bottom: 2px;
}

.menu-details{
  margin:35px;
}
.menu_name_list div p{
  /* background-color: violet;  */
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0%;
  left: 0%;
  /* transform: translate(-50%,20%); */
  cursor: pointer;
  padding-top: 40%;
  /* text-align: center; */
}

.closebutton{
  color:white;
  width:1.2rem;
  height:1.2rem;
  cursor:pointer;
  filter: invert(100%) sepia(0%) saturate(1071%) hue-rotate(186deg) brightness(115%) contrast(100%);
}